@font-face {
  font-family: "roboto-example";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/RobotoRegular/RobotoRegular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "roboto-example";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/RobotoBold/RobotoBold.woff2") format("woff2");
  font-display: swap;
}

html,
body {
  height: 100%;
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

body {
  font-family: "roboto-example", sans-serif;
  font-size: 16px;
  color: orange;
  line-height: 1.65;
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
  opacity: 1;
}

section {
  position: relative;
}

.visually-hidden:not(:focus):not(:active),
.svg-placeholder {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

:root {
  --base: yellow;
  --spacing: 10px;
  --blur: 10px;
}

.change-variables {
  text-align: center;
  background: #193549;
  color: white;
  font-family: 'helvetica neue', sans-serif;
  font-weight: 100;
  font-size: 24px;
}

.change-variables .controls {
  margin-bottom: 50px;
}

.change-variables .hl {
  color: var(--base);
}

.change-variables img {
  padding: var(--spacing);
  background: var(--base);
  filter: blur(var(--blur));
}

.change-variables input {
  width: 100px;
}

body.drum-kit {
  font-size: 10px;
  background: url(https://cdn.wallpapersafari.com/66/11/ADqdce.jpg) bottom center;
  background-size: cover;
}

body.drum-kit .keys {
  display: flex;
  flex: 1;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}

body.drum-kit .key {
  border: .4rem solid black;
  border-radius: .5rem;
  margin: 1rem;
  font-size: 1.5rem;
  padding: 1rem .5rem;
  transition: all .07s ease;
  width: 10rem;
  text-align: center;
  color: white;
  background: rgba(0, 0, 0, 0.4);
  text-shadow: 0 0 .5rem black;
}

body.drum-kit .playing {
  transform: scale(1.1);
  border-color: #ffc600;
  box-shadow: 0 0 1rem #ffc600;
}

body.drum-kit kbd {
  display: block;
  font-size: 4rem;
}

body.drum-kit .sound {
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: #ffc600;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.flex-gallery {
  margin: 0;
  box-sizing: border-box;
  background: #ffc600;
  font-family: 'helvetica neue',sans-serif;
  font-size: 20px;
  font-weight: 200;
}

.flex-gallery .panels {
  min-height: 100vh;
  overflow: hidden;
  display: flex;
}

.flex-gallery .panel {
  cursor: pointer;
  box-shadow: inset 0 0 0 5px rgba(255, 255, 255, 0.1);
  color: white;
  text-align: center;
  align-items: center;
  /* Safari transitionend event.propertyName === flex */
  /* Chrome + FF transitionend event.propertyName === flex-grow */
  transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s;
  font-size: 20px;
  background-size: cover;
  background: #6B0F9C center;
  flex-grow: 1;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.flex-gallery .panel1 {
  background-image: url(https://source.unsplash.com/gYl-UtwNg_I/1500x1500);
}

.flex-gallery .panel2 {
  background-image: url(https://source.unsplash.com/rFKUFzjPYiQ/1500x1500);
}

.flex-gallery .panel3 {
  background-image: url(https://images.unsplash.com/photo-1465188162913-8fb5709d6d57?ixlib=rb-0.3.5&q=80&fm=jpg&crop=faces&cs=tinysrgb&w=1500&h=1500&fit=crop&s=967e8a713a4e395260793fc8c802901d);
}

.flex-gallery .panel4 {
  background-image: url(https://source.unsplash.com/ITjiVXcwVng/1500x1500);
}

.flex-gallery .panel5 {
  background-image: url(https://source.unsplash.com/3MNzGlQM7qs/1500x1500);
}

.flex-gallery .panel > * {
  margin: 0;
  width: 100%;
  transition: transform 0.5s;
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-gallery .panel > *:first-child {
  transform: translateY(-100%);
}

.flex-gallery .panel > *:last-child {
  transform: translateY(100%);
}

.flex-gallery .panel.open-active > *:first-child {
  transform: translateY(0);
}

.flex-gallery .panel.open-active > *:last-child {
  transform: translateY(0);
}

.flex-gallery .panel p {
  text-transform: uppercase;
  font-family: 'Amatic SC', cursive;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
  font-size: 2em;
}

.flex-gallery .panel p:nth-child(2) {
  font-size: 4em;
}

.flex-gallery .panel.open {
  flex-grow: 5;
  font-size: 40px;
}

.home {
  text-align: center;
  background: #eee;
  max-width: 1200px;
  margin: 0 auto;
}

.posts_wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.posts_wrapper article {
  flex-basis: 300px;
  border: 1px solid #193549;
  border-radius: 3px;
  box-shadow: 3px 3px 5px #193549;
  background: #3137ff;
}

.posts_wrapper article a {
  color: #EFEFEF;
  text-decoration: none;
  text-transform: uppercase;
}

body.js-clock {
  background: #018DED url("../img/sky_blue_white.jpg") no-repeat;
  background-size: auto;
  font-family: 'helvetica neue', sans-serif;
  text-align: center;
  font-size: 10px;
}

body.js-clock .clock-wrapper {
  margin: 0;
  font-size: 2rem;
  display: flex;
  flex: 1;
  min-height: 100vh;
  align-items: center;
}

body.js-clock .clock {
  width: 30rem;
  height: 30rem;
  border: 20px solid white;
  border-radius: 50%;
  margin: 50px auto;
  position: relative;
  padding: 2rem;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.1), inset 0 0 0 3px #EFEFEF, inset 0 0 10px black, 0 0 10px rgba(0, 0, 0, 0.2);
}

body.js-clock .clock-face {
  position: relative;
  width: 100%;
  height: 100%;
  transform: translateY(-3px);
  /* account for the height of the clock hands */
}

body.js-clock .hand {
  width: 50%;
  height: 6px;
  background: black;
  position: absolute;
  top: 50%;
  transform-origin: 100%;
  transform: rotate(90deg);
  transition: all .05s;
  transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
}